
.profile{
    margin-top: 1px;
    /* border: solid; */
    padding: 80px 20px 20px 10px;
    border-radius: 10px;
    background-color: #fff;
   
}
.left-side {
    float: left;
    height: 80vh;
  }
  .right-side {
    float: right    ;
  }
  label {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
    width: 100%;
    max-width: 500px;
  }
  
  input {
    padding: 0.5rem;
    font-size: 2rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-top: 0.5rem;
  }
  
  button {
    background-color: #007bff;
    color: #fff;
    padding: 1.5rem 8rem;
    font-size: 1rem;
    border: none;
    border-radius: 4px;
    margin-top: 600px;
    cursor: pointer;
    /* margin-bottom: 300px; */
  }
  
  button:hover {
    background-color: #0069d9;
  }
 